import React from 'react';
import {observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter, Link} from "react-router-dom";
import {
	Button,
	Modal,
	Divider,
	Row,
	Col
} from 'antd';

@withRouter
@observer
class OrderSuccess extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			visible: false
		}
	}


	componentDidMount() {

	}

	render() {
		const {visible, salesObj, handleView, onClose} = this.props;
		let fields = JSON.parse(JSON.stringify(salesObj));
		const {customerCompany, deliveryAddress, customerPeople, customerPhone, agreedTime, serial, placeOrderTime} = fields;

		return (
			<Modal
				closable={false}
				footer={null}
				title={null}
				visible={visible}
				width="686px"
			>
				<div className="pr"
				     style={{
					     paddingLeft: "89px"
				     }}>
					<img
						style={{
							position: "absolute",
							top: "18px",
							left: "18px"
						}}
						src="./images/order_success.png" alt="" width="49" height="49"/>
					<div>
						<p style={{
							fontFamily: 'PingFangSC-Regular',
							fontWeight: 400,
							fontStyle: "normal",
							fontSize: "22px",
							color: " #5BBE99",
							height: "51px",
							lineHeight: "51px"
						}}>下单成功</p>
						<p className="order_success_p">我们将联系厂家尽快排产，</p>
						<p className="order_success_p">您可关注小程序，随时查看订单状态</p>
					</div>
					<img
						style={{
							position: "absolute",
							top: "0",
							right: "40px"
						}}
						src="./images/order_wechat.png" alt="" width="111" height="111"/>
				</div>
				<Divider
					style={{
						margin: "55px 0 20px"
					}}
					dashed/>
                <Row className="order_success_title">
	                <Col span={12}>
                       <span className="color_7f">客户单位：</span> <span >{customerCompany}</span>
	                </Col>
	                <Col span={12}>
		                <span className="color_7f">要货地址：</span> <span>{deliveryAddress}</span>
	                </Col>
	                <Col span={12}>
		                <span className="color_7f">客户对接人：</span> <span>{customerPeople}  {customerPhone}</span>
	                </Col>
	                <Col span={12}>
		                <span className="color_7f">要货时间：</span> <span>{moment(agreedTime).format('YYYY-MM-DD')}</span>
	                </Col>
	                <Col span={12}>
		                <span className="color_7f">订单号：</span> <span>{serial}</span>
	                </Col>
	                <Col span={12}>
		                <span className="color_7f">下单时间：</span> <span>{moment(placeOrderTime).format('YYYY-MM-DD HH:mm')}</span>
	                </Col>
                </Row>
				<Row className="mt50 mb30">
					<Col span={24} className="tc">
						<Button type="default" onClick={onClose}>返回</Button>
						<Button type="primary" style={{margin: "0 20px"}} onClick={handleView}>查看订单</Button>
					</Col>
				</Row>
			</Modal>
		)
	}
}


export default OrderSuccess;



