import React from 'react';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import * as mobx from 'mobx';
import {withRouter} from "react-router-dom";
import {Breadcrumb, DatePicker, Row, Col, Form, Input, InputNumber, message, Radio, Button, Icon, Popconfirm,Spin, Affix} from 'antd';
import OrderSuccess from './OrderSuccess';
import {List} from 'immutable';
import MyUpload from "@/components/common/MyUpload";
import {v4 as uuidv4} from 'uuid';
import url from 'url';
import Zmage from 'react-zmage';
import Unity, { UnityContent } from "react-unity-webgl";
const {TextArea} = Input;
const obj = {add: "新增", edit: "编辑", view: "查看"};
import styles from './styles.less';
window.unityCommand=null;
window.unityContent = null;

let unityContent = new UnityContent (
	'./unitywebgl/UnityBuild/ncop.json',
	'./unitywebgl/UnityBuild/UnityLoader.js',
);

unityContent.on('CallFuncJS', (paraName) => {
	console.log('CallFuncJS===========>',paraName)
	// eval(paraName);
});

let unityCommand = new Map();


@inject('OrderStore')
@withRouter
@Form.create()
@observer
export default class SalesDetal extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.OrderStore;
		this.state = {
			visible: false,  // 下单成功
			status: "add",
			logoUrls: [],   // 企业logo
			tip: '正在生成请稍等...',
			isLoading: false
		}
	}

	componentWillReceiveProps(nextProps, nextContext) {
		if (nextProps.location.search!==this.props.location.search) {
			let search = nextProps.location.search;
			const status = url.parse(search, true).query.status;
			this.setState({
				status: status,
			});
		}
	}

	componentDidMount() {
		// 获取大文件上传sts
		this.store.getSts();
		let search = this.props.location.search;
		const status = url.parse(search, true).query.status;
		const id = url.parse(search, true).query.id;
		this.setState({
			status: status,
			logoUrls: [],   // 企业logo
			visible: false,  // 下单成功
			isLoading: false
		}, ()=>{
            this.init(status, id)
		});


	}

	init = (status, id) => {
     if (status=="view"||status=="edit") { // 查看
     	this.store.getSalesOrderById({id: id}).then((res)=>{
     		if (res!=null) {
                this.store.salesObj = Object.assign({}, res)
		        this.setState({
			        logoUrls: res.logoUrl == null||res.logoUrl=="" ? [] : [{
				        uid:  "!" + uuidv4(),
				        name: this.transTxt(res),
				        status: 'done',
				        url: res.logoUrl,
				        fileId: "!" + uuidv4(),
				        fileName: this.transTxt(res),
				        filePath: res.logoUrl,
				        id:  "!" + uuidv4(),
			        }]
		        });
	        }
        })
     } else if (status=="add") {  // 新增
	     this.store.newSalesOrder().then((res)=>{
		     if (res!=null) {
			     this.store.salesObj = Object.assign(res, {
				     "orderLabor": res.orderLabor ==null?[{
					     "id":"",
					     "mainId":"",
					     "size":"6055*2990*3000mm",
					     "naked":"是",
					     "channelType":"翼闸",
					     "lineEmbedded":"是",
					     "safetyDoor":"是",
					     "channelEmbedded":"是",
					     "roomPlace":"靠左",
					     "haveFurniture":"是",
					     "counts":1,
					     "otherAsk":"",
					     "rendering":"",
					     "ncopModelId":"",
					     "atts":[],
					     "number":""
				     }]:res.orderLabor,
				     "constructionConditions":  res.constructionConditions==null?{
					     "id":"",
					     "mainId":"",
					     "isokBasis":"",
					     "isokRoad":"",
					     "isokLifting":"",
					     "isokElectricity":"",
					     "atts":[]
				     }:Object.assign(res.constructionConditions, {
					     "atts": res.constructionConditions.atts==null?[]:res.constructionConditions.atts
				     })
			     });
			     this.setState({
				     logoUrls: res.logoUrl == null||res.logoUrl=="" ? [] : [{
					     uid:  "!" + uuidv4(),
					     name: this.transTxt(res),
					     status: 'done',
					     url: res.logoUrl,
					     fileId: "!" + uuidv4(),
					     fileName: this.transTxt(res),
					     filePath: res.logoUrl,
					     id:  "!" + uuidv4(),
				     }]
			     });
		     }
	     });
     }
	}

	// 详情复制
	handleAdd = (type) => {
		let customList = JSON.parse(JSON.stringify(this.store.salesObj[type]));
		let len = customList.length;
        let current = customList[len - 1];
		this.handleJust(current, ()=>{
            if (current.rendering==null||current.rendering=="") {
	            message.warn("效果图是必填项");
	            return false;
            }
			// 复制数组的最后一个
			this.store.salesObj[type] = [...customList, current];
		});
	}

	// 删除复制
	handleDelete = (type, index) => {
		let customList = JSON.parse(JSON.stringify(this.store.salesObj[type]));
		let list = List(customList);
		this.store.salesObj[type] = Object.assign([], list.delete(index).toJS())
	}

	// 文本
	handleChange = (type, field, index, e) => {
		const value = typeof e.target.type == "text" ? e.target.value : e.target.value;
		let customList = JSON.parse(JSON.stringify(this.store.salesObj[type]));
		let list = List(customList);
		// 全高闸特殊控制 联动
		if (field == "channelType"&&value == "全高闸") {
			list.update(index, (val) => {
				val["safetyDoor"] = "是";
				val[field] = value;
				return val;
			});
			this.props.form.setFieldsValue({
				["safetyDoor"+index]: "是"
			});
		} else {
			list.update(index, (val) => {
				val[field] = value;
				return val;
			});
		}
		this.store.salesObj[type] = Object.assign([], list.toJS());
	}

	// number
	handleNumChange = (type, field, index, e) => {
		const value = e;
		let customList = JSON.parse(JSON.stringify(this.store.salesObj[type]));
		let list = List(customList);
		list.update(index, (val) => {
			val[field] = value;
			return val;
		});
		this.store.salesObj[type] = Object.assign([], list.toJS());
	}

	handleChange2 = (type, field, e) => {
		let obj = JSON.parse(JSON.stringify(this.store.salesObj[type]));
		obj[field] = e.target.value;
		this.store.salesObj[type] = Object.assign({}, obj);
	}

	handleSubmit = (e) => {
		e.preventDefault();

		let salesObj = JSON.parse(JSON.stringify(this.store.salesObj));
		// 照片校验
		if (salesObj.constructionConditions.atts==null||salesObj.constructionConditions.atts.length==0) {
			message.warn("照片是必填项");
			return false;
		}

		this.props.form.validateFields((err, values) => {
			console.log(err, values, '保存')
			if (!err) {
				const {projectName, customerCompany, deliveryAddress, channel, customerPeople, channelPeople, customerPhone, channelPhone, agreedTime} = values;
				const params = Object.assign(salesObj, {
					projectName,
					customerCompany,
					deliveryAddress,
					channel,
					customerPeople,
					channelPeople,
					customerPhone,
					channelPhone,
					agreedTime: moment(agreedTime).valueOf(),
					constructionConditions: Object.assign(mobx.toJS(salesObj.constructionConditions), {
						atts: mobx.toJS(salesObj.constructionConditions).atts == null ? [] : mobx.toJS(salesObj.constructionConditions).atts.map(item => Object.assign({
							fileId: item.fileId,
							fileName: item.fileName,
							filePath: item.filePath,
							id: item.id
						})),
					})
				})
				this.store.saveSalesOrder({
					order: params,
					saveType: 1
				}).then((res) => {
					if (res) {
						message.success('保存成功');
                        // 更新数据
						this.store.salesObj = Object.assign({}, res);
					}
				});
			}
		})
	}

	// 立即下单
	handleOrder = (e) => {
		e.preventDefault();
		let salesObj = JSON.parse(JSON.stringify(this.store.salesObj));
		// 照片校验
		if (salesObj.constructionConditions.atts==null||salesObj.constructionConditions.atts.length==0) {
			message.warn("照片是必填项");
			return false;
		}
		this.props.form.validateFields((err, values) => {
			if (!err) {
				const {projectName, customerCompany, deliveryAddress, channel, customerPeople, channelPeople, customerPhone, channelPhone, agreedTime} = values;
				const params = Object.assign(salesObj, {
					projectName,
					customerCompany,
					deliveryAddress,
					channel,
					customerPeople,
					channelPeople,
					customerPhone,
					channelPhone,
					agreedTime: moment(agreedTime).valueOf(),
					placeOrderTime: mobx.toJS(salesObj).placeOrderTime==0?null:mobx.toJS(salesObj).placeOrderTime,
					constructionConditions: Object.assign(mobx.toJS(salesObj.constructionConditions), {
						atts: mobx.toJS(salesObj.constructionConditions).atts == null ? [] : mobx.toJS(salesObj.constructionConditions).atts.map(item => Object.assign({
							fileId: item.fileId,
							fileName: item.fileName,
							filePath: item.filePath,
							id: item.id
						})),
					})
				})
				this.store.saveSalesOrder({
					order: params,
					saveType: 1
				}).then((res) => {
					if (res) {
						// 下单接口
						this.store.buildPurchasingOrder({salesOrderId: res.id}).then((res) => {
							if (res) {
								this.setState({
									visible: true,
								});
								// 更新销售订单数据
								this.store.salesObj = Object.assign({}, res);
							} else {
								message.error("下单失败")
							}
						})
					}
				});
			}
		})
	}

	handleAttment = (type, params) => {
		// 处理附件
		let arr = [];
		params.length && params.map((item) => {
			arr.push({
				fileId: item.fileId,
				fileName: item.fileName,
				filePath: item.filePath,
				id: item.id,
				uid: item.uid,
				name: item.fileName,
				status: 'done',
				url: item.filePath,
			})
		});

		let obj = JSON.parse(JSON.stringify(this.store.salesObj["constructionConditions"]));
		obj["atts"] = Object.assign([], arr);
		this.store.salesObj["constructionConditions"] = Object.assign({}, obj);
	}

	handleLogo = (type, params) => {
		// 处理附件
		let arr = [];
		params.length && params.map((item) => {
			arr.push({
				fileId: item.fileId,
				fileName: item.fileName,
				filePath: item.filePath,
				id: item.id,
				uid: item.uid,
				name: item.fileName,
				status: 'done',
				url: item.filePath,
			})
		});

		let obj = JSON.parse(JSON.stringify(this.store.salesObj));
		obj.logoUrl = arr.length==0?"":arr[0].filePath;
		this.store.salesObj = Object.assign({}, obj);
		this.setState({
			logoUrls: arr
		})
	}

	handleBack = () => {
		this.props.history.push("/order?key=1");
	}

	transTxt = (fields) => {
		let logoName;
		if (fields.logoUrl!=null&&fields.logoUrl!="") {
			//获取最后一个.的位置
			var index= fields.logoUrl.lastIndexOf(".");
			//获取后缀
			var ext = fields.logoUrl.substr(index+1);
			logoName = "企业logo."+ext;
		}
		return logoName;
	}

	// 修改
	handleEdit = () => {
		const {isSales, salesObj} = this.store;
		let obj = JSON.parse(JSON.stringify(salesObj));
		const {createUserId} = obj;
		const userid = localStorage.getItem('userid');
		if (userid!=createUserId&&isSales) {
           message.warn("您没有编辑权限！");
           return false;
		}
		this.props.history.push("/order/sales?status=edit")
	}

	onClose = () => {
		this.setState({
			visible: false
		}, ()=>{
           this.handleBack();
		});
	}

	handleView = () => {
		this.setState({
			visible: false
		}, ()=>{
			this.props.history.push("/order/sales?status=view")
		});
	}

	// 生成效果图
	buildDesignSketch = (index) => {
		let customList = JSON.parse(JSON.stringify(this.store.salesObj["orderLabor"]));
		let current = customList[index];
		let elementId = 'PSE8C0EHISSEMDSA';
        this.handleJust(current, ()=>{
			unityContent.on('GetUrlParamFun', (paraName) => {
                return elementId
            })
            unityContent.on('progress', (progression)=>{
				if(progression===1){
					this.snapshotCommand({'current':['尺寸', current.size, '闸机种类', current.channelType, '是否配安全门', current.safetyDoor, '保安室位置', current.roomPlace, '是否裸仓', current.naked, '穿线管预埋', current.lineEmbedded, '闸机固定预埋', current.channelEmbedded, '家具配备', current.haveFurniture],'familyId':elementId})
				}
			})

        	this.store.buildDesignSketch({labor: current}).then((res)=>{
		        if (res) {
			        this.getReportUrl(res, index);
		        }
	        });
        });
	}

	// 生成效果图
	updateDesignSketch = (index) => {
		let customList = JSON.parse(JSON.stringify(this.store.salesObj["orderLabor"]));
		let current = customList[index];
		let elementId = 'PSE8C0EHISSEMDSA';
		this.handleJust(current, ()=>{
			this.snapshotCommand({'current':['尺寸', current.size, '闸机种类', current.channelType, '是否配安全门', current.safetyDoor+'', '保安室位置', current.roomPlace, '是否裸仓', current.naked, '穿线管预埋', current.lineEmbedded, '闸机固定预埋', current.channelEmbedded, '家具配备', current.haveFurniture],'familyId':elementId})
			this.store.buildDesignSketch({labor: current}).then((res)=>{
				if (res) {
					this.getReportUrl(res, index);
				}
			});
		});
	}

	// 间隔3秒获取pdf地址
	getReportUrl = (id, index) => {
		let _this = this;
		let formData = JSON.parse(JSON.stringify(_this.store.salesObj));
		let orderLabor = formData.orderLabor;
		const params = {
			id: id
		}
		this.setState({
			isLoading: true
		});
		_this.timer = setInterval(() => {
			_this.store.getDownlaodUrl(params).then((res) => {
				const url = res;
				if (url !== null) {
					this.setState({
						isLoading: false
					});
					// 保存效果图
					orderLabor[index].rendering = url||"http://crawl.ws.126.net/c97cc8142a1331b24bc156eeaf111fee.jpg";
					_this.store.salesObj = Object.assign(formData, {
						orderLabor: orderLabor
					});

					clearInterval(_this.timer);
				}
			});
		}, 3000);
	}

	// 校验
	handleJust = (record, callback) => {
		const {size, channelType, safetyDoor, roomPlace, naked, lineEmbedded, channelEmbedded, haveFurniture, counts} = record;
		if (size == null || size == "") {
			message.warn("尺寸是必填项")
			return false;
		}
		if (naked==null||naked=="") {
			message.warn("是否裸仓是必填项")
			return false;
		}
		if (channelType == null || channelType == "") {
			message.warn("闸机种类是必填项")
			return false;
		}
		if (safetyDoor==null||safetyDoor=="") {
			message.warn("安全门是必填项")
			return false;
		}
		if (roomPlace==null||roomPlace=="") {
			message.warn("保安室位置是必填项")
			return false;
		}
		if (lineEmbedded==null||lineEmbedded=="") {
			message.warn("穿线管预埋是必填项")
			return false;
		}
		if (channelEmbedded==null||channelEmbedded=="") {
			message.warn("闸机固定预埋是必填项")
			return false;
		}
		if (counts==null||counts=="") {
			message.warn("数量是必填项")
			return false;
		}
		if (haveFurniture==null||haveFurniture=="") {
			message.warn("家具配备是必填项")
			return false;
		}
		if (typeof callback=="function") {
			callback();
		}
	}

	fullscreen = () => {
        unityContent.setFullscreen(true);
	}

	snapshotCommand = (params) =>{
		var command = {
			"func": "ExecuteCommand",
			"param": {
			 "id": "1",
			 "command": "ElementTool.SnapshotCommand",
			 "parameters": [
				{"name":"snapshot","type":"string","value":"false"},
				{
				"name": "familyId",
				"type": "string",
				"value": params.familyId
				}, {
				"name": "cartesian",
				"type": "string",
				"value": JSON.stringify(params.current)
				}
			  ]
		    }
		}
		this.unityQuery(command);
	}

	unityQuery = (command) => {
        console.log('command=====================>',JSON.stringify(command));

		unityContent.send('UIHandler', 'JSQuery', JSON.stringify(command));
	}

	render() {
		const {visible, status, logoUrls} = this.state;
		const {salesObj, ossData, purchaseObj} = this.store;
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				md: {span: 8},
				sm: {span: 8},
			},
			wrapperCol: {
				xs: {span: 24},
				md: {span: 16},
				sm: {span: 16},
			},
		};
		let fields = mobx.toJS(salesObj);  // 基本信息
		let orderLabor = fields.orderLabor || []; // 订单详情字段
		let constructionConditions = fields.constructionConditions || {}; // 现场施工条件字段
		const hidden = status=="view"?true:false;
		const attachments = constructionConditions.atts == null ? [] : constructionConditions.atts.map((vitem, vindex) => {
			return Object.assign({}, {
				uid: vitem.id == null || vitem.id == "" ? "!" + uuidv4() : vitem.id,
				name: vitem.fileName,
				status: 'done',
				url: vitem.filePath,
				fileId: vitem.id == null ? "!" + uuidv4() : vitem.id,
				fileName: vitem.fileName,
				filePath: vitem.filePath,
				id: vitem.id == null ? "!" + uuidv4() : vitem.id,
			})
		})
		return (
			<div className="pb50">
				<Affix offsetTop={10}>
					<div className={styles.webglContent}>
						{/*<Unity unityContext={unityContext} />*/}
					</div>
				</Affix>
				<Breadcrumb separator=">">
					<Breadcrumb.Item>
						<a href='#/order'>{'订单管理'}</a>
					</Breadcrumb.Item>
					<Breadcrumb.Item>
						<a href='#/order?key=1'>{'销售订单'}</a>
					</Breadcrumb.Item>
					<Breadcrumb.Item>
						{obj[status]}订单
					</Breadcrumb.Item>
				</Breadcrumb>
				<div className="mt30">
					<h3 className="kanban_title mb20">基本信息</h3>
					<Form {...formItemLayout} className="ant-advanced-search-form">
						<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 12}, 15]} style={{padding: 0, margin: "0"}}>
							<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
								<Form.Item label="工程名称" className="w">
									{getFieldDecorator('projectName', {
										initialValue: fields.projectName || "",
										rules: [{required: false, message: '此项是必填'}],
									})(
										<Input placeholder="请输入" disabled={hidden}/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
								<Form.Item label="客户单位" className="w">
									{getFieldDecorator('customerCompany', {
										initialValue: fields.customerCompany || "",
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Input placeholder="请输入" disabled={hidden}/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
								<Form.Item label="要货地址" className="w">
									{getFieldDecorator('deliveryAddress', {
										initialValue: fields.deliveryAddress || "",
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Input placeholder="请输入" disabled={hidden}/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
								<Form.Item label="渠道" className="w">
									{getFieldDecorator('channel', {
										initialValue: fields.channel || "",
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Input placeholder="请输入" disabled={hidden}/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
								<Form.Item label="客户对接人" className="w">
									{getFieldDecorator('customerPeople', {
										initialValue: fields.customerPeople || "",
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Input placeholder="请输入" disabled={hidden}/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
								<Form.Item label="渠道对接人" className="w">
									{getFieldDecorator('channelPeople', {
										initialValue: fields.channelPeople || "",
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Input placeholder="请输入" disabled={hidden}/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
								<Form.Item label="客户电话" className="w">
									{getFieldDecorator('customerPhone', {
										initialValue: fields.customerPhone || "",
										rules: [{required: true, message: '此项是必填'}, {pattern: /^1[3|4|5|6|7|8|9][0-9]{9}$/, message: '请输入正确的手机号'}],
									})(
										<InputNumber className="w" placeholder="请输入" disabled={hidden}/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
								<Form.Item label="渠道电话" className="w">
									{getFieldDecorator('channelPhone', {
										initialValue: fields.channelPhone || "",
										rules: [{required: true, message: '此项是必填'}, {pattern: /^1[3|4|5|6|7|8|9][0-9]{9}$/, message: '请输入正确的手机号'}],
									})(
										<InputNumber className="w" placeholder="请输入" disabled={hidden}/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
								<Form.Item label="要货时间" className="w">
									{getFieldDecorator('agreedTime', {
										initialValue: fields.agreedTime == null || fields.agreedTime == 0 ? "" : moment(moment(fields.agreedTime).format('YYYY-MM-DD'), "YYYY-MM-DD"),
										rules: [{required: true, message: '此项是必填'}],
									})(
										<DatePicker
											className="w"
											placeholder="请选择"
											format="YYYY-MM-DD"
											disabled={hidden}/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
								<Form.Item label="企业logo" className="w">
									{getFieldDecorator('logoUrl', {
										initialValue: fields.logoUrl || "",
										rules: [{required: false, message: '请输入'}],
									})(
										hidden?(fields.logoUrl==null||fields.logoUrl==""?<span>无</span>:<Zmage width="64" height="64" src={fields.logoUrl} alt=""/>):<MyUpload
												saveAttachment={this.handleLogo.bind(this, 'logoUrl')}
												disabled={hidden}
												multiple={true}
												accept={".jpg,.jpeg,.gif,.png,.bmp"}
												listType="picture-card"
												ossData={mobx.toJS(ossData)}
												fileList={logoUrls}
												length={1}
											/>
									)}
								</Form.Item>
							</Col>
							{fields.id!=null&&fields.id!=""?<Col xs={24} sm={24} md={18} lg={12} xl={12} className={fields.id!=null&&fields.id!=""?"":"hidden"} style={{padding: 0}}>
									<Form.Item label="订单编号" className="w">
										{getFieldDecorator('serial', {
											initialValue: fields.serial || "",
											rules: [{required: fields.id==null||fields.id==""?false:true, message: '此项是必填'}],
										})(
											<Input className="w" placeholder="请输入" disabled={true}/>
										)}
									</Form.Item>
								</Col>:null}
							{fields.id!=null&&fields.id!=""?<Col xs={24} sm={24} md={18} lg={12} xl={12} className={fields.id!=null&&fields.id!=""?"":"hidden"} style={{padding: 0}}>
								<Form.Item label="订单时间" className="w">
									{getFieldDecorator('placeOrderTime', {
										initialValue: fields.placeOrderTime || "",
										rules: [{required: false, message: '此项是必填'}],
									})(
										<Input className="w" placeholder="请输入" disabled={true}/>
									)}
								</Form.Item>
							</Col>:null}
						</Row>
					</Form>
				</div>
				{/*订单详情*/}
				<div className="mt30">
					<h3 className="kanban_title mb20">订单详情</h3>
					{orderLabor.map((item, index) => {
						return <Form
							wrappedComponentRef={(form) => this["form" + (index + 1)] = form}
							className="orderLabor_form ant-advanced-search-form"
							{...formItemLayout}
							style={{borderBottom: "1px dashed #e6e6e6"}}>
							{hidden?null:<Popconfirm
								title="确定要删除吗?"
								onConfirm={this.handleDelete.bind(this, "orderLabor", index)}
								onCancel={() => {
								}}
								okText="是"
								cancelText="否"
								placement="topLeft"
							>
								<Icon type="close"
								      className={index == 0 ? "hidden" : "orderLabor_form_delete"}/>
							</Popconfirm>}
							<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 12}, 15]} style={{margin: "0"}}>
								<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
									<Form.Item label="尺寸" className="w">
										{getFieldDecorator('size' + index, {
											initialValue: item.size || "6055*2990*3000mm",
											rules: [{required: true, message: '此项是必填'}],
										})(
											<Radio.Group disabled={hidden}
												onChange={this.handleChange.bind(this, 'orderLabor', 'size', index)}>
												<Radio value={"6055*2990*3000mm"}>6055*2990*3000mm</Radio>
												<Radio value={"7500*2990*3000mm"}>7500*2990*3000mm</Radio>
											</Radio.Group>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
									<Form.Item label="是否裸仓" className="w">
										{getFieldDecorator('naked' + index, {
											initialValue: item.naked || "是",
											rules: [{required: true, message: '此项是必填'}],
										})(
											<Radio.Group disabled={hidden}
												onChange={this.handleChange.bind(this, 'orderLabor', 'naked', index)}>
												<Radio value="是">是</Radio>
												<Radio value={"否"}>否</Radio>
											</Radio.Group>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
									<Form.Item label="闸机种类" className="w">
										{getFieldDecorator('channelType' + index, {
											initialValue: item.channelType || "翼闸",
											rules: [{required: true, message: '此项是必填'}],
										})(
											<Radio.Group disabled={hidden}
												onChange={this.handleChange.bind(this, 'orderLabor', 'channelType', index)}>
												<Radio value={"翼闸"}>翼闸</Radio>
												<Radio value={"全高闸"}>全高闸</Radio>
											</Radio.Group>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
									<Form.Item label="穿线管预埋" className="w">
										{getFieldDecorator('lineEmbedded' + index, {
											initialValue: item.lineEmbedded || "是",
											rules: [{required: true, message: '此项是必填'}],
										})(
											<Radio.Group
												disabled={hidden}
												onChange={this.handleChange.bind(this, 'orderLabor', 'lineEmbedded', index)}>
												<Radio value={"是"}>是</Radio>
												<Radio value="否">否</Radio>
											</Radio.Group>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
									<Form.Item label="是否配安全门" className="w">
										{getFieldDecorator('safetyDoor' + index, {
											initialValue: item.safetyDoor || "是",
											rules: [{required: true, message: '此项是必填'}],
										})(
											hidden?<Radio.Group disabled={true}
											                    onChange={this.handleChange.bind(this, 'orderLabor', 'safetyDoor', index)}>
												<Radio value="是">是（全高闸必须配备安全门）</Radio>
												<Radio value="否">否</Radio>
											</Radio.Group>: <Radio.Group
												             disabled={item.channelType == "全高闸"}
												             onChange={this.handleChange.bind(this, 'orderLabor', 'safetyDoor', index)}>
													<Radio value="是">是（全高闸必须配备安全门）</Radio>
													<Radio value="否">否</Radio>
												</Radio.Group>)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
									<Form.Item label="闸机固定预埋" className="w">
										{getFieldDecorator('channelEmbedded' + index, {
											initialValue: item.channelEmbedded || "是",
											rules: [{required: true, message: '此项是必填'}],
										})(
											<Radio.Group disabled={hidden}
												onChange={this.handleChange.bind(this, 'orderLabor', 'channelEmbedded', index)}>
												<Radio value={"是"}>是</Radio>
												<Radio value={"否"}>否</Radio>
											</Radio.Group>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
									<Form.Item label="保安室位置" className="w">
										{getFieldDecorator('roomPlace' + index, {
											initialValue: item.roomPlace || "靠左",
											rules: [{required: true, message: '此项是必填'}],
										})(
											<Radio.Group
												disabled={hidden}
												onChange={this.handleChange.bind(this, 'orderLabor', 'roomPlace', index)}>
												<Radio value={"靠左"}>靠左</Radio>
												<Radio value={"靠右"}>靠右</Radio>
											</Radio.Group>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
									<Form.Item label="家具配备" className="w">
										{getFieldDecorator('haveFurniture' + index, {
											initialValue: item.haveFurniture || "是",
											rules: [{required: true, message: '此项是必填'}],
										})(
											<Radio.Group
												disabled={hidden}
												onChange={this.handleChange.bind(this, 'orderLabor', 'haveFurniture', index)}>
												<Radio value={"是"}>是</Radio>
												<Radio value={"否"}>否</Radio>
											</Radio.Group>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
									<Form.Item label="数量" className="w">
										{getFieldDecorator('counts' + index, {
											initialValue: item.counts || "1",
											rules: [{required: true, message: '此项是必填'}],
										})(
											<InputNumber
												disabled={hidden}
												onChange={this.handleNumChange.bind(this, 'orderLabor', 'counts', index)}
												className="w" placeholder="请输入" disabled={hidden}/>
										)}
									</Form.Item>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
									<Spin tip={this.state.tip} spinning={this.state.isLoading} delay={100}>
									{/* <Form.Item label={<span><span style={{
										fontWeight: "400",
										marginRight: "4px",
										color: "#f5222d",
										fontSize: "14px",
										fontFamily: "SimSun, sans-serif"
									}}>*</span>效果图</span>} className="w">
										{getFieldDecorator('rendering', {
											initialValue: item.rendering || "",
											rules: [{required: false, message: '此项是必填'}],
										})(
											item.rendering==null||item.rendering==""?
												<Button type="primary" icon="cloud-upload" onClick={this.buildDesignSketch.bind(this, index)}>生成效果图</Button>:
												<Zmage width="64" height="64" src={item.rendering}/>
										)}
									</Form.Item> */}
									<Form.Item label={<span><span style={{
										fontWeight: "400",
										marginRight: "4px",
										color: "#f5222d",
										fontSize: "14px",
										fontFamily: "SimSun, sans-serif"
									}}>*</span>效果图</span>} className="w">
										{getFieldDecorator('rendering', {
											initialValue: item.rendering || "",
											rules: [{required: false, message: '此项是必填'}],
										})(
											item.rendering==null||item.rendering==""?
												<Button type="primary" icon="cloud-upload" onClick={this.buildDesignSketch.bind(this, index)}>生成效果图</Button>:
												<div className="webglContent" >
													<Unity className="unityZone" unityContent={unityContent}/>
													<div className="footer">
													<Button style={{backgroundColor:'#1eaed3'}} type="primary" icon="cloud-sync" onClick={this.updateDesignSketch.bind(this, index)}>更新配置</Button><Button style={{backgroundColor:'#1eaed3', float:'right', marginTop:'4px'}}  type="primary" icon="fullscreen" title="全屏"   onClick={this.fullscreen}></Button>
													</div>
												</div>


										)}
									</Form.Item>
									</Spin>
								</Col>
								<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
									<Form.Item label="其他要求" className="w">
										{getFieldDecorator('otherAsk' + index, {
											initialValue: item.otherAsk || "",
											rules: [{required: false, message: '此项是必填'}],
										})(
											<TextArea
												disabled={hidden}
												onChange={this.handleChange.bind(this, 'orderLabor', 'otherAsk', index)}
												rows={4} disabled={hidden} placeholder="请输入"/>
										)}
									</Form.Item>
								</Col>
							</Row>
						</Form>
					})}

					{/*复制添加*/}
					<Form {...formItemLayout} className={hidden?"hidden":"ant-advanced-search-form"}>
						<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 12}, 15]} style={{margin: "0"}}>
							<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
								<Form.Item label="复制添加" className="w">
									<Icon
										onClick={this.handleAdd.bind(this, "orderLabor")}
										style={{
											fontSize: "26px",
											color: "#1890ff",
											cursor: "pointer",
											position: "relative",
											top: "5px"
										}}
										type="plus-circle"/>
								</Form.Item>
							</Col>
						</Row>
					</Form>
				</div>
				{/*现场施工条件*/}
				<div className="mt30">
					<h3 className="kanban_title mb20">现场施工条件</h3>
					<Form className="ant-advanced-search-form" {...formItemLayout}>
						<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 12}, 15]} style={{margin: "0"}}>
							<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
								<Form.Item label="基础" className="w">
									<span className="mr15">基础硬化且强度达标</span>
									{getFieldDecorator('isokBasis', {
										initialValue: constructionConditions.isokBasis || "",
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Radio.Group disabled={hidden}
											onChange={this.handleChange2.bind(this, 'constructionConditions', 'isokBasis')}>
											<Radio value={"是"}>是</Radio>
											<Radio value={"否"}>否</Radio>
										</Radio.Group>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
								<Form.Item label="临电" className="w">
									<span className="mr15">具备通电条件</span>
									{getFieldDecorator('isokElectricity', {
										initialValue: constructionConditions.isokElectricity || "",
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Radio.Group disabled={hidden}
											onChange={this.handleChange2.bind(this, 'constructionConditions', 'isokElectricity')}>
											<Radio value={"是"}>是</Radio>
											<Radio value={"否"}>否</Radio>
										</Radio.Group>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
								<Form.Item label="道路" className="w">
									<span className="mr15">周边道路满足17.5米板车通行</span>
									{getFieldDecorator('isokRoad', {
										initialValue: constructionConditions.isokRoad || "",
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Radio.Group disabled={hidden}
											onChange={this.handleChange2.bind(this, 'constructionConditions', 'isokRoad')}>
											<Radio value={"是"}>是</Radio>
											<Radio value={"否"}>否</Radio>
										</Radio.Group>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
								<Form.Item label="吊装" className="w">
									<span className="mr15">满足25t吊车站位</span>
									{getFieldDecorator('isokLifting', {
										initialValue: constructionConditions.isokLifting || "",
										rules: [{required: true, message: '此项是必填'}],
									})(
										<Radio.Group disabled={hidden}
											onChange={this.handleChange2.bind(this, 'constructionConditions', 'isokLifting')}>
											<Radio value={"是"}>是</Radio>
											<Radio value={"否"}>否</Radio>
										</Radio.Group>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12} style={{padding: 0}}>
								<Form.Item
								           label={<span><span style={{
									           fontWeight: "400",
									           marginRight: "4px",
									           color: "#f5222d",
									           fontSize: "14px",
									           fontFamily: "SimSun, sans-serif"
								           }}>*</span>照片</span>}
								           className="w">
									{getFieldDecorator('atts', {
										//initialValue: attachments||[],
										rules: [{
											required: false,
											message: '此项是必填'}],
									})(
										<MyUpload
											key="atts"
											saveAttachment={this.handleAttment.bind(this, 'atts')}
											disabled={hidden}
											multiple={true}
											accept={".jpg,.jpeg,.gif,.png,.bmp"}
											listType="picture-card"
											ossData={mobx.toJS(ossData)}
											fileList={attachments}
										/>
									)}
								</Form.Item>
							</Col>
						</Row>
					</Form>
				</div>
				<Row className="mt20">
					<Col span={24} className="tc">
						<Button type="default" onClick={this.handleBack}>返回</Button>
						<Button className={hidden?"":"hidden"} type="primary" style={{margin: "0 20px"}} onClick={this.handleEdit}>修改订单</Button>
						<Button className={hidden?"hidden":""} type="primary" style={{margin: "0 20px"}} onClick={this.handleSubmit}>保存</Button>
						<Button className={hidden?"hidden":""} type="primary" onClick={this.handleOrder}>立即下单</Button>
					</Col>
				</Row>
				<OrderSuccess
					visible={visible}
					salesObj={salesObj}
					onClose={this.onClose}
					handleView={this.handleView}
				/>
			</div>
		)
	}
}






